import {AppRegistry} from 'react-native';
import App from './App';
import {name as appName} from './app.json';
import {Script, ScriptManager} from '@callstack/repack/client';
import RNFS from 'react-native-fs';

ScriptManager.shared.addResolver(async scriptId => {
  // In development, get all the chunks from dev server.
  const fileName = `${RNFS.DocumentDirectoryPath}/${scriptId}.chunk.bundle`;
  console.log('__DEV__', __DEV__);
  if (__DEV__) {
    return {
      url: Script.getDevServerURL(scriptId),
      cache: false,
    };
  }

  // const fileName = `https://raw.githubusercontent.com/trinmdotwork/finan-chunk/refs/heads/main/${scriptId}.chunk.bundle`;

  console.log(fileName);

  const file = `file://${fileName}`;

  return {
    url: file,
    cache: true,
    absolute: true,
  };
});

AppRegistry.registerComponent(appName, () => App);
