var registry=new Map();var register=function register(id){var used=registry.get(id);if(used!=null){registry.set(id,used+1);}else{registry.set(id,1);}};var unregister=function unregister(id){var used=registry.get(id);if(used!=null){if(used<=1){registry.delete(id);}else{registry.set(id,used-1);}}};var has=function has(id){return registry.get(id)||false;};module.exports={register:register,unregister:unregister,has:has};