'use strict';var androidScaleSuffix={'0.75':'ldpi','1':'mdpi','1.5':'hdpi','2':'xhdpi','3':'xxhdpi','4':'xxxhdpi'};var ANDROID_BASE_DENSITY=160;function getAndroidAssetSuffix(scale){if(scale.toString()in androidScaleSuffix){return androidScaleSuffix[scale.toString()];}if(Number.isFinite(scale)&&scale>0){return Math.round(scale*ANDROID_BASE_DENSITY)+'dpi';}throw new Error('no such scale '+scale.toString());}var drawableFileTypes=new Set(['gif','jpeg','jpg','ktx','png','svg','webp','xml']);function getAndroidResourceFolderName(asset,scale){if(!drawableFileTypes.has(asset.type)){return'raw';}var suffix=getAndroidAssetSuffix(scale);if(!suffix){throw new Error("Don't know which android drawable suffix to use for scale: "+scale+'\nAsset: '+JSON.stringify(asset,null,'\t')+'\nPossible scales are:'+JSON.stringify(androidScaleSuffix,null,'\t'));}return'drawable-'+suffix;}function getAndroidResourceIdentifier(asset){return(getBasePath(asset)+'/'+asset.name).toLowerCase().replace(/\//g,'_').replace(/([^a-z0-9_])/g,'').replace(/^(?:assets|assetsunstable_path)_/,'');}function getBasePath(asset){var basePath=asset.httpServerLocation;return basePath.startsWith('/')?basePath.slice(1):basePath;}module.exports={getAndroidResourceFolderName:getAndroidResourceFolderName,getAndroidResourceIdentifier:getAndroidResourceIdentifier,getBasePath:getBasePath};